.textarea{
    font-family: inherit;
    font-size: 100%;
}

form{
    overflow: auto;
}

form textarea{
    width: 100%;
    outline: none;
    border: none;
    border-bottom: 1px solid lightgray;
    padding: 12px 20px 12px 0;
    background-color: transparent;
}

.commentBox{
    position: relative;
}

.commentLine{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-color: black;
    transform: scaleX(0);
    animation: commentLineScaleIn 0.2s forwards;
}

@keyframes commentLineScaleIn{
    0%{
        transform: scaleX(0);
    }
    100%{
        transform: scaleX(1);
    }
}

.commentButton{
    font-family: inherit;
    font-size: 0.9rem;
    font-weight: bold;
    float: right;
    border: none;
    margin: 10px 0 0 10px;
    padding: 10px 20px;
    border-radius: 2px;
}

.sendButton{
    background-color: #065fd4;
    color: white;
}

.sendButton:disabled{
    color: white;
    cursor: default;
    background-color: #cccccc;
}

.messageContainer{
    position: relative;
    width: 100%;
    word-wrap: break-word;
    margin: 20px 0;
    padding-left: 50px;
}

.fa-user-circle{
    position: absolute;
    font-size: 2.2rem;
    top: 0;
    left: 0;
}

.messageUser{
    font-weight: bold;
    font-size: 0.9rem;
}

.messageText{
    font-size: 0.9rem;
    margin: 5px 0;
}

.messageIconContainer{
    margin-top: 10px;
}

.messageIconContainer > *{
    display: inline-block;
    color: gray;
    margin-right: 10px;
    cursor: pointer;
}

.arrowComment{
    margin-top: 10px;
    cursor: pointer;
}

.arrowComment > *{
    color: #065fd4;
    display: inline-block;
}

.arrowComment > i{
    margin-right: 10px;
}

.username{
    position: absolute;
    font-size: 1.1rem;
    top: 0;
    left: 0;
}